@import '../../styles/palette.scss';

p {
    .review-card-review {
        font-size: 1em;
        margin: 2.5em;
        color: $text-color3;    
    }
}

.review-card-img {
    width: 60%;
}

.review-card-name {
    color: $text-color3;
    text-decoration: underline solid $green-color1;
    font-style: italic;
    font-size: 3em;
    font-weight:400;
    text-transform: uppercase;
    text-shadow: 1px -1px 0px rgba(150, 150, 150, 1), 2px -2px 0px #2d2d2d;
    margin:0;
}

.review-card-description {
    color: $text-color1;
    font-size: 1.2em;
}

.review-card-closed {
    margin: 3px 20%;
    border: 4px solid black;
    background-color:#b38b5e;
    background-size: 100%;
    transition: all 0.5s ease;
}

.review-card-location-info {
    width: 50%;
    margin: 0 25% 30px 25%;
    text-align: right;
    color: $text-color2;
    font-size: 0.8em;
}

.review-card-open {
    margin: 0 20% 0 20%;
    border: 4px solid black;    
    border-radius: 20px;
    background-color: #b38b5e;
    transition:    border-radius 0.3s ease-in-out, 
    box-shadow 0.2s ease-in-out, border 0.5s ease-in-out;
    ;
}

@media (min-device-width : 100px) 
and (max-device-width : 1000px)  {
    p {
        font-size: 1em;
        margin: 1em;
        color: $text-color3;
    
    }
    
    .review-card-img {
        width: 80%;
    }
    
    .review-card-name {
        color: $text-color3;
        text-decoration: underline solid $green-color1;
        font-style: italic;
        font-size: 3em;
        font-weight:400;
        text-transform: uppercase;
        text-shadow: 1px -1px 0px rgba(150, 150, 150, 1), 2px -2px 0px #2d2d2d;
        margin:0;
    }
    
    .review-card-closed {
        margin: 3px 5%;
        border: 4px solid black;
        color: white;
        background-size: 100%;
        transition: all 0.5s ease;
    }
    
    .review-card-location-info {
        width: 80%;
        margin: 0 10% 30px 10%;
        text-align: right;
        color: $text-color2;
        font-size: 0.8em;
    }
    
    .review-card-open {
        margin: 3px 5% 3px 5%;
        border: 4px solid black;
        border-radius: 20px;
        background-color: #b38b5e;
        transition:    border-radius 0.3s ease-in-out, 
        box-shadow 0.2s ease-in-out, border 0.5s ease-in-out;
        ;
    }
}


